%base {
  font-size: 1rem;
  line-height: 1.1; }

.locations-module-list {
  overflow: hidden;
  font-size: 0;
  letter-spacing: 0;
  text-align: center;
  margin-left: -30px;
  margin-bottom: 20px; }
.locations-module-list_item {
  @extend %base;
  width: 50%;
  padding-left: 30px;
  text-align: left;
  display: inline-block;
  margin-bottom: 30px;
  transition: all .15s ease;

  &:hover {
    cursor: pointer;
    transform: scale(1.03); } }

@keyframes location-item-beacon {
  0% {
    opacity: 0;
    transform: scale(0); }
  30% {
    opacity: 0;
    transform: scale(0); }
  60% {
    transform: scale(1); }
  65% {
    opacity: 1; }
  100% {
    opacity: 0;
    transform: scale(1.8); } }

.location-item {
  position: relative; }

.location-item_beacon {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  border-radius: 15px;
  position: relative;
  margin-right: 5px;
  margin-left: -5px;
  &:after {
    content: ' ';
    width: 8px;
    height: 8px;
    border-radius: 5px;
    background: red;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    animation: location-item-beacon .8s linear infinite; } }

.location-item_beacon--neutral {
  &:after {
    background: yellow; } }

@media (max-width: 730px) {
  .locations-module-list {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto; }
  .locations-module-list_item {
    width: 100%;
    padding-left: 0; } }
.locations-module-map {
  margin-bottom: 50px; }

@keyframes location-detail-in {
  0% {
    opacity: 0;
    transform: translateX(30px); }
  100% {
    opacity: 1;
    transform: translateX(0px); } }

.locations-detail {
  overflow: hidden;
  margin-bottom: 30px; }
.js-location-container {
  display: none; }
.locations-detail_back {
  width: 10%;
  float: left;
  padding-left: 30px;
  font-size: 80px;
  line-height: 40px;
  font-weight: 200;
  transition: all .15s ease;
  animation: location-detail-in .3s .5s ease-out backwards;
  &:hover {
    cursor: pointer;
    transform: translateX(-10px) scale(1.03); } }
.locations-detail_location {
  width: 35%;
  float: left;
  margin-bottom: 20px;
  animation: location-detail-in .3s .3s ease-out backwards;
  padding-right: 15px; }
.locations-detail_map {
  width: 55%;
  float: left;
  margin-bottom: 20px;
  animation: location-detail-in .3s ease-out backwards; }

@media (max-width: 980px) {
  .locations-detail_back {
    width: 100%;
    float: none;
    text-align: center;
    max-width: 60px;
    margin: 0 auto;
    margin-bottom: 30px; }
  .locations-detail_location {
    width: 100%;
    float: none;
    text-align: center; }
  .locations-detail_map {
    width: 100%;
    float: none; } }




.trucklot-menu-items {}
.trucklot-menu-item {
  overflow: hidden;
  padding-bottom: 10px;
  font-size: 0;
  letter-spacing: 0;
  text-align: center;
  &:after {
    content: '';
    display: block;
    width: 100%;
    margin-top: 10px;
    border-bottom: 1px solid currentcolor;
    opacity: 0.1; }
  > * {
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    @extend %base; } }
.trucklot-menu-item_content {
  overflow: hidden;
  width: calc(100% - 300px); }
.trucklot-menu-item_name {}
.trucklot-menu-item_price {
  float: right;
  padding: 0 0 20px 20px;
  opacity: 0.8; }
.trucklot-menu-item_desc {}
.trucklot-menu-item_image {
  padding-right: 30px;
  max-width: 300px;
  text-align: center; }
.trucklot-menu + .trucklot-menu {
  padding-top: 40px; }
@media (max-width: 800px) {
  .trucklot-menu-item_content {
    width: 100%; }
  .trucklot-menu-item_image {
    width: 100%;
    padding: 0;
    text-align: center;
    margin-bottom: 15px; } }
